* {
    caret-color: transparent;
}

#main {
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: 15vh;
    touch-action: manipulation;
}

#calculator {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    width: 300px;
    height: 400px;
    min-width: 250px;
    min-height: 300px;
    border: 1px solid rgb(0,0,0,.3);
    border-radius: 20px;
    box-shadow: 0px 0px 18px 0px rgba(0,0,0,0.75);
}

#row {
    display: flex;
    width: 13vw;
    min-width: 250px;
    align-items: center;
    justify-content: center;
}

button {
    width: 28vw;
    height: 60px;
    border: 1px solid black;
    font-size: 14px;
    outline: none;
}

button:active {
    background: black;
    color: white;
}

#display {
    display: flex;
    margin-top: 1vh;
    margin-bottom: 10px;
    width: 13vw;
    min-width: 250px;
    height: 35px;
    border: 1px solid rgb(0,0,0,.5);
    box-shadow: inset 0px 0px 2px 0px rgba(0,0,0,.75);
    align-items: center;
    justify-content: flex-end;
}

#displaytext {
    padding-right: 5px;
    font-family: Roboto-regular;
    font-size: 15px;
    -webkit-appearance: none;
}

